.scrollbar::-webkit-scrollbar{display:none}html,body{font-family:"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;width:100vw;padding:0;margin:0}#root-div{padding:20px 0 0 0;width:100vw}.container,.self-info-sect{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}label{display:flex;font-weight:500;font-weight:16.5px;line-height:35px;height:35px;width:100%}input:not([type=radio]){width:98%;height:30px;line-height:30px;padding:0 8px;box-sizing:border-box;outline:none;border:0;border-bottom:1.6px dashed #666;text-align:center}.radio-group{width:100%;box-sizing:border-box;padding:0 9%;display:flex;justify-content:space-around;align-items:center}select{width:90%;margin-left:5%;background-color:rgba(0,0,0,0);height:30px;line-height:30px;text-align:center;outline:none;border:0;border-bottom:1px dashed #666}select option{background-color:#fff;color:#333;padding:2px 10px}textarea{width:100%;height:200px;box-sizing:border-box;padding:6px 12px;resize:none;border:1px dashed #666;border-radius:12px}.self-info-sect>section{width:84%;min-width:300px;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;margin:4px 0}footer{padding:10px 0 60px 0;display:flex;justify-content:center;align-items:center;flex-direction:column}footer .link{margin:30px 0}footer>button{box-sizing:border-box;text-align:center;font-weight:600;font-size:16px;border:0;padding:12px 23px;border-radius:20px;color:#fff;background-color:#409eff}.must{font-size:25px;color:#a80000}.word-info{display:inline-block;margin:0 5px;transform:scale(0.85);color:#555}.tip-span{margin-right:20px;margin-left:auto;font-size:14px;color:#b00}@media(min-width: 700px){.self-info-sect section{width:500px !important}}