.honors-box{
    width: 100vw;
    padding: 40px 0;
    position: relative;
    z-index: 1;
    
    .top-area{
        width: 100vw;
        height: calc(1.64 * 40vw);
        display: flex;
        justify-content: center;
        align-items: center;

        .right-imgs{
            position: relative;
            width: 60vw;
            
            img{
                position: absolute;
                top: 50%;
                right: 0;
                transform: translate(0%, -50%);
                width: 40vw;
                max-width: 300px;
                border-radius: 6px;
                transform-origin: right bottom;
                border: 1px solid rgb(237, 237, 213);
                transition: all 400ms linear 0s;
            }
            img:nth-child(1){
                z-index: 6;
            }
            @for $i from 0 to 4{
                .ver-img-#{$i}{
                    z-index: #{5 - $i} !important;
                    transform: translate(0, -55%) rotate(#{$i * (-4)}deg);
                    opacity: #{1 - $i * 0.3};
                }
            }
        }
        .left-title{
            writing-mode: vertical-rl;
            text-align: center;
            font-size: 1.5rem;
        }
    }
    @media (min-width: 850px) {
        .top-area{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: auto !important;
            padding: 30px 0;

            .left-title{
                writing-mode: horizontal-tb;
            }
            .right-imgs{
                width: 70vw;
                height: 70vh;
                max-width: 840px;
                max-height: 600px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}