.footer-box{
    width: 100vw;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f1f6ff;
    position: relative;
    // border-radius: 30% 30% 0 0;

    .main-content-box{
        width: 100vw;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .address-and-postcode{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    }
    .attention-relative-box{
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 300ms linear 0s;

        .attention,.relative{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
    
            .rel-img-group{
                box-sizing: border-box;
                padding: 0 20px;
                width: 100%;
                display: flex;
                justify-content: space-around;
                align-items: center;
            }
            img{
                width: 30vw;
                margin: 0 20px;
            }
        }
    }
    .address,.postcode,.copyright,.record{
        width: 95vw;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        p{
            margin: 5px 0;
            width: 95vw;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .copyright{
        margin-top: 15px;
    }
    .record{
        margin-top: 15px;
        font-size: .8rem;
        opacity: 0.7;

        img{
            width: 13px;
            margin-right: 10px;
        }
    }
    .footer-img-box{
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;

        .end-text-img{
            width: 65%;
            max-width: 600px;
        }
    }
    
    .code-img{
        max-width: 160px;
    }
}

// 780px 往下
@media (max-width: 780px) {
    .attention-relative-box{
        flex-direction: column;
    }
}

// 780 到 1050
@media (min-width: 780px) and (max-width: 1050px) {
    .attention-relative-box{
        flex-direction: row;
        justify-content: space-around !important;
        padding: 0 5vw;
        box-sizing: border-box;
    }
    br{
        display: none;
    }
    .record{
        flex-direction: row !important;

        p{
            display: inline !important;
            width: 300px !important;
        }
    }
}

// 1050 到
@media (min-width: 1050px) and (max-width: 1300px) {
    .attention-relative-box{
        flex-direction: row;
        justify-content: space-around !important;
        padding: 0 5vw;
        box-sizing: border-box;
    }
    br{
        display: none;
    }
    .address,.postcode{
        flex-direction: row !important;

        p{
            margin-left: 18px !important;
            width: auto !important;
        }
    }
    .record{
        flex-direction: row !important;

        p{
            display: inline !important;
            width: 300px !important;
        }
    }
}

@media (min-width: 1300px) {
    .footer-box{
        align-items: flex-start !important;
        box-sizing: border-box;
        padding: 0 5vw;
    }
    .main-content-box,.attention-relative-box{
        width: calc(100vw - 450px - 10vw) !important;
    }
    .footer-img-box{
        position: absolute;
        right: 5vw;
        top: 0;
        width: 450px !important;
        height: 250px;
        display: flex;
        justify-content: center;
        align-items: center;

        .end-text-img{
            width: 450px !important;
        }
    }
    .attention-relative-box{
        flex-direction: row;
        justify-content: space-around !important;
        padding: 0 5vw;
        box-sizing: border-box;
    }
    br{
        display: none;
    }
    .address-and-postcode{
        position: relative;
        left: 0;
        transform: translate(calc(2.5vw + 225px), 0);
        width: calc(100vw - 10vw);
        flex-direction: row !important;

        .address,.postcode{
            width: auto !important;
            flex-direction: row !important;
    
            h3{
                margin: 10px 0;
            }
            p{
                margin-left: 18px !important;
                width: auto !important;
            }
        }
        .postcode{
            margin-left: 40px;
        }
    }
    .copyright{
        margin-top: 0 !important;
        width: 90vw !important;
        position: relative;
        left: 0;
        transform: translate(calc(2.5vw + 225px), 0);
        width: calc(100vw - 10vw);
        flex-direction: row !important;
        justify-content: center;

        p:nth-child(1){
            width: 320px;
        }
        p{
            display: inline-block !important;
            width: auto;
            max-width: 620px;
            padding: 0;
        }
    }
    .record{
        flex-direction: row !important;

        p{
            display: inline !important;
            width: 300px !important;
        }
    }
}

