.joinus-box{
    width: 100vw;
    // height: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    font-family: 'font-eng-2';

    .condition{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .collection{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            p{
                font-size: 1.2rem;
                margin: 10px 0;
            }
            .point-to{
                width: 100px;
                transform: rotate(165deg);
            }
            .to-collect{
                position: relative;
                width: 65vw;
                max-width: 300px;
                padding: 20px 0;
                box-sizing: border-box;
                border-radius: 12px;
                display: flex;
                justify-content: center;
                align-items: center;

                button{
                    padding: 10px 18px;
                    border-radius: 6px;
                    font-size: 1.1rem;
                }
            }
            .to-collect::after{
                animation: clippath 3s linear -1.5s infinite;
            }
            .to-collect::before{
                animation: clippath 3s linear infinite;
            }
            .to-collect::before,.to-collect::after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border: 2px solid rgba(112,149,249,.333);
                transition: all .5s;
                border-radius: 10px;
            }
            .to-collect:hover{
                cursor: pointer;
            }
        }
    }
}

@keyframes clippath {
    0%, 100%{
        -webkit-clip-path: inset(0 0 90% 0);
        clip-path: inset(0 0 90% 0);
    }
    25%{
        -webkit-clip-path: inset(0 90% 0 0);
        clip-path: inset(0 90% 0 0);
    }
    50%{
        -webkit-clip-path: inset(90% 0 0 0);
        clip-path: inset(90% 0 0 0);
    }
    75%{
        -webkit-clip-path: inset(0 0 0 90%);
        clip-path: inset(0 0 0 90%);
    }
}
