.second-box{
    width: 100vw;
    // height: 100vh;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    margin-top: 30px;

    .bg-pattern-box{
        z-index: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100%;
        padding: 0;
        overflow-x: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 1;
        transition: opacity 500ms linear 0s;

        img{
            width: 75vw;
            height: 75vw;
            opacity: 0.3;
        }
    }
    @media (min-width: 620px) {
        .bg-pattern-box{
            opacity: 0;
        }
    }
}